import axios from 'axios'
import React, { useState } from 'react'
import { LOGIN, THIS_HOST } from '../hosts'
import './Login.css'

function Login() {
    const [email, setEmail] = useState('')
    const [password, setPasswrod] = useState('')
    const [error, setError] = useState(false)

    const hanldeLogin = () => {
        axios.post(LOGIN, { email, password })
            .then(res => {
                if (res.data.success) { 
                    localStorage.setItem('email', email); 
                    localStorage.setItem('pass', password);
                    window.location.href = THIS_HOST
                }else{
                    setError("wrong credentials")
                }
            })
    }
    return (
        <div className='page-sgszbp'>
            <h1>Login</h1>
            {error && <div className='error-xuthnv'>{error}</div>}
            <div className='inputs-nyawxa'>
                <input type="text" onChange={(ev) => setEmail(ev.target.value)} value={email} />
                <input type="password" onChange={(ev) => setPasswrod(ev.target.value)} value={password} />
                <button onClick={hanldeLogin}>Login</button>
            </div>
        </div>
    )
}

export default Login