import React, { useState, Suspense } from 'react';
import './App.css';
import Login from './components/Login';
import F404 from './pages/404';
import { Routes, Route } from 'react-router-dom'
import Navbar from './components/Navbar'
import './sharedStyle.css'
const Home = React.lazy(() => import('./pages/Home'))
const Pages = React.lazy(() => import('./pages/Pages'))
const URLS = React.lazy(() => import('./pages/URLS'))
const Logs = React.lazy(() => import('./pages/Logs'))

function App() {
  const [email] = useState(localStorage.getItem('email'))
  const [pass] = useState(localStorage.getItem('pass'))
  return (
    <div className="App">
      {!isSecret() && <Login />}
      {isSecret() && <>
        <Navbar />
        <Routes>
          <Route path={'/'} element={<Suspense fallback={<>Loading ...</>}><Home /></Suspense>} />
          <Route path={'/pages'} element={<Suspense fallback={<>Loading ...</>}><Pages /></Suspense>} />
          <Route path={'/urls'} element={<Suspense fallback={<>Loading ...</>}><URLS /></Suspense>} />
          <Route path={'/logs'} element={<Suspense fallback={<>Loading ...</>}><Logs /></Suspense>} />
          <Route path={'/*'} element={<F404 />} />
        </Routes></>}
    </div>
  );

  function isSecret() {
    if ((email && email.length > 0) & (pass && pass.length > 0)) return true
    return false
  }
}

export default App;
