import React from 'react'
import './Navbar.css'
import {Link} from 'react-router-dom'

import { THIS_HOST } from '../hosts'


function Navbar() {
    return (
        <div className='navbar-hnixuk'>
            <div className="logout">
                <button onClick={logout} className='kzpdsa'>logout</button>
            </div>

            <div className='links-container-ruiltg'>
                <ul className='links-aysyce'>
                    <li className='link-cont-ndehri'><Link className='nav-link-ykbacx' to={'/'}>Home</Link></li>
                    <li className='link-cont-ndehri'><Link className='nav-link-ykbacx' to={''}></Link></li>
                    <li className='link-cont-ndehri'><Link className='nav-link-ykbacx' to={''}></Link></li>
                </ul>
            </div>
        </div>
    )
}


function logout() {
    localStorage.clear()
    window.location.href = THIS_HOST
}

export default Navbar